* {
  box-sizing: border-box;
}

body {
  color: #212121;
  background-color: #fafafa;
  font-family: Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px solid #7b1f1f;
  border-bottom-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.cat-info {
  align-items: center;
  display: flex;
}

.cat-info img {
  width: 100%;
  margin: 0;
  padding-top: 20px;
  padding-right: 10px;
}

p {
  text-align: justify;
}

h3 {
  font-weight: bold;
}

